





























import Vue from 'vue';
import apolloQueries from '../apolloQueries'

export default Vue.extend({
  name: 'AppToolbar',
  data() {
    return {
      mostRecentEventQuery: apolloQueries.mostRecentEvent
    }
  },
  methods: {
    mostRecentEvent(data: any) {
      return data.mostRecentEvent.items[0].events.reverse()[0]
    }
  }
})
